<template>
  <div>
    <div class="navbarFixed" v-if="navbarFixed">
      <div class="width-3 center">
        <TipoDePago/>
        <Plazo/>
      </div>
    </div>
    <div>
      <TipoDePago/>
      <Plazo/>
    </div>
  </div>
</template>
<script>
import TipoDePago from "./OpcionesEstaticas/TipoDePago";
import Plazo from "./OpcionesEstaticas/Plazo";

export default {
  components: {
    TipoDePago,
    Plazo
  },
  computed: {
    navbarFixed() {
      if (this.scroll >= 290) {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    scrollUpdate() {
      this.scroll = window.scrollY;
    }
  },
  mounted() {
    window.addEventListener("scroll", this.scrollUpdate, false);
  }
};
</script>
