<script>
import {mapState} from 'vuex'
export default {
  props: ["banco"],
  data() {
    return {
      imagenesDeBancos: {
        banamex: "logo-banamex.gif",
        banorte: "logo-banorte.gif",
        banregio: "logo-banregio.gif",
        hsbc: "logo-hsbc.gif",
        santander: "logo-santander.gif",
        scotiabank: "logo-scotiabank.gif"
      }
    };
  },
  computed: {
    creditoPreautorizado() {
      if (this.tipoDeIngresoSeleccionado == "Asalariado") {
        return parseFloat(this.ingresoMensual) * 50;
      } else {
        return parseFloat(this.ingresoMensual) * 36;
      }
    },
    ...mapState(["tipoDeIngresoSeleccionado","ingresoMensual","bancos"])
  },
  watch:{
      bancos(){
          this.$forceUpdate()
      }
  }
};
</script>
